<template>
  <div class="page">
    <header>
      <Topbar/>
    </header>
    <main>
      <Editor/>
      <Preview/>
    </main>
  
  </div>
</template>

<script>
import 'normalize.css/normalize.css'
import './assets/reset.css'

import Topbar from './components/Topbar'
import Editor from './components/Editor'
import Preview from './components/Preview'
import icons from './assets/icons'

import store from './store/index'

export default {
  name: 'app',
  
  store,


  components: { Topbar, Editor, Preview },

  created() {
    document.body.insertAdjacentHTML('afterbegin', icons) //
  }
}
</script>

<style lang="scss">
.page {
  height: 100vh;
  display: flex;
  flex-direction: column;
  background: #EAEBEC;


  >main {
    flex-grow: 1;
  }

  >main {
    min-width: 1024px;
    max-width: 1440px;
    margin-top: 16px;
    margin-bottom: 16px;
    display: flex;
    justify-content: space-between;
    padding: 0 16px;
    width: 100%;
    align-self: center;
  }

  #editor {
    min-width: 35%;
    background: #444;
  }

  #preview {
    flex-grow: 1;
    margin-left: 16px;
    background: #777;
  }
}

svg.icon {
  height: 1em;
  width: 1em;
  fill: currentColor;
  vertical-align: -0.1em;
  font-size: 16px;
}
</style>



